	module.exports = {"data":{"markdownRemark":{"html":"<p>前回日記に引き続きあれこれ成分分析しまくってみるテスト。</p>\n<h2>ﾊﾞﾌｧﾘﾝの成分解析結果</h2>\n<p>ﾊﾞﾌｧﾘﾝの74%は気の迷いで出来ています。 ﾊﾞﾌｧﾘﾝの15%は魔法で出来ています。 ﾊﾞﾌｧﾘﾝの9%は知恵で出来ています。 ﾊﾞﾌｧﾘﾝの2%は花崗岩で出来ています。 …つまり、あのCMはウソツキだったんだよ！</p>\n<h2>小麦ちゃんの成分解析結果</h2>\n<p>小麦ちゃんの51%は着色料で出来ています。 小麦ちゃんの20%は微妙さで出来ています。 小麦ちゃんの15%は小麦粉で出来ています。 小麦ちゃんの14%は鉄の意志で出来ています。 さすが名前の通り。</p>\n<h2>玄爺の成分解析結果</h2>\n<p>玄爺の97%は怨念で出来ています。 玄爺の2%は柳の樹皮で出来ています。 玄爺の1%は真空で出来ています。 霊夢に捨てられた恨みは大きい…と。</p>\n<h2>ベルナルデュスの成分解析結果</h2>\n<p>ベルナルデュスの69%は祝福で出来ています。 ベルナルデュスの13%は小麦粉で出来ています。 ベルナルデュスの10%は不思議で出来ています。 ベルナルデュスの4%はスライムで出来ています。 ベルナルデュスの2%はかわいさで出来ています。 ベルナルデュスの1%は魔法で出来ています。 ベルナルデュスの1%はマイナスイオンで出来ています。 流石ハッピーおじさん。</p>\n<h2>ｽﾋﾟﾘﾀｽの成分解析結果</h2>\n<p>ｽﾋﾟﾘﾀｽの80%は毒物で出来ています。 ｽﾋﾟﾘﾀｽの19%は砂糖で出来ています。 ｽﾋﾟﾘﾀｽの1%は気合で出来ています。 ……だいたいあってる。</p>\n<h2>ＫＯＲＧ ＯＡＳＹＳの成分解析結果</h2>\n<p>ＫＯＲＧ ＯＡＳＹＳの半分はハッタリで出来ています。 ＫＯＲＧ ＯＡＳＹＳの19%は保存料で出来ています。 ＫＯＲＧ ＯＡＳＹＳの13%は華麗さで出来ています。 ＫＯＲＧ ＯＡＳＹＳの8%は勢いで出来ています。 ＫＯＲＧ ＯＡＳＹＳの6%は宇宙の意思で出来ています。 ＫＯＲＧ ＯＡＳＹＳの4%は陰謀で出来ています。 'Ａ｀</p>\n<h2>'Ａ｀の成分解析結果</h2>\n<p>'Ａ｀の96%は嘘で出来ています。 'Ａ｀の3%は心の壁で出来ています。 'Ａ｀の1%は欲望で出来ています。 'Ａ｀</p>\n<h2>東方プロジェクトの成分解析結果</h2>\n<p>東方プロジェクトはすべて嘘で出来ています。 'Ａ｀</p>","frontmatter":{"cover":null,"date":"2006-03-29T02:39:00+09:00","strDate":"2006/3/28","title":"成分分析にハマる"}}},"pathContext":{"path":"/ja/blog/2006/03/29-成分分析にハマる/","langKey":"ja"}}

